body, h1, h2, h3, h4, h5, h6, p, ul, li, a {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
}

.img__logo {
  height: 40px;
  margin-left: 50px;
}

.nav {
  margin-right: 20px;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

[data-qa="hover"]:hover {
  color: var(--blue-color);
}
